<template>
   <div class="gymAuthentication">
       <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle">拳馆认证</span>
            <span class="back"></span>
        </div>
        <form class="gymInfo">
            <div class="gymName">
                <label>拳馆名称</label>
                <input type="text" v-model="infoForm.gymName">
            </div>
            <div class="contact">
                <label>联系号码</label>
                <input type="number" v-model="infoForm.contact">
            </div>
            <div class="curatorName">
                <label>馆长姓名</label>
                <input type="text" v-model="infoForm.curatorName">
            </div>
            <div class="idNum">
                <label>身份证号</label>
                <input type="text" maxlength="18" v-model="infoForm.idNum">
            </div>
            <button class="nextBtn" @click="confirm">下一步</button>
        </form>
   </div>
</template>

<script>
import { uploadParam } from '../../api/getdata'
import Qs from 'qs'
import { Toast } from 'mint-ui'
export default {
  name: 'gymAuthentication',
  data () {
    return {
      infoForm: {
        gymName: '', contact: '', curatorName: '', idNum: ''
      }
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    confirm () {
      let idCardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
      if (idCardReg.test(this.infoForm.idNum) && this.infoForm.gymName && this.infoForm.contact && this.infoForm.curatorName) {
        let data = uploadParam(Qs.stringify({
          'name': this.infoForm.gymName, 'number': this.infoForm.contact, 'clubName': this.infoForm.curatorName, 'idenTityNo': this.infoForm.idNum
        }))
        data.then(res => {
          console.log(res)
          if (res.status === '0') {
            Toast({
              message: '提交成功',
              duration: 1000
            })
            this.$router.push('/home/enroll/coachStep2')
          }
        })
      } else {
        Toast({
          message: '输入的格式不正确',
          duration: 1500
        })
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.5rem;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .gymInfo {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 4.48rem;
  }

  .gymInfo label {
    display: inline-block;
    width: 2rem;
  }

  .gymName,
  .contact,
  .curatorName,
  .idNum {
    display: flex;
    justify-content: space-between;
    padding: 0.2rem;
    margin-top: 0.4rem;
  }

  .gymInfo input {
    width: calc(100% - 2.4rem);
    border: none;
    border-bottom: 1px solid #666666;
    outline: none;
  }

  .nextBtn {
    width: 100%;
    height: 1.33333rem;
    border-radius: 0.15rem;
    background: #ffd200;
    border: none;
    outline: none;
    margin-top: 2.613333rem;
    font-weight: 400;
  }
</style>



